/* Add your own global styles here */

body {
    font-family: 'Arial', sans-serif;
    background-color: #f8f9fa;
  }
  
  .card {
    width: 18rem;
    border: 1px solid #ced4da;
    border-radius: 8px;
    margin: 20px;
    overflow: hidden;
    transition: transform 0.3s ease-in-out;
  }
  
  .card:hover {
    transform: scale(1.05);
  }
  
  .card-img-top {
    max-height: 200px;
    object-fit: cover;
  }
  
  .card-body {
    padding: 16px;
  }
  
  .card-title {
    font-size: 1.25rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #343a40;
  }
  
  .card-text {
    font-size: 1rem;
    color: #6c757d;
  }
  
  .footer {
    color: #6c757d;
  }
  
  .read-more {
    background-color: #343a40;
    border-color: #343a40; 
    color: #ffffff; 
    width: 100%;
  }
  
  .btn-dark:hover {
    background-color: #212529;
    border-color: #212529; 
  }
  
  
  @media (max-width: 768px) {
    .card {
      width: 100%;
    }
  }
  